.product-wrapper {
    width: 250px;
    height: 350px;
    padding: 0 4px;
    position: relative;
}



.produvt-thumbnail {
    width: 100%;
}
.product-detail__line1 {
    text-transform: uppercase;
    font-size: 11px;
    color: #9d9c9c;
}

.product-detail__line2 {
    margin: 0;
    font-size: 14px;
    margin-bottom: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 1;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    /* width: 90%;
    margin: auto; */
}

.product-detail__line3 {
    display: flex;
    justify-content: center;
    align-items: center;
}

.product-detail__line3-current {
    padding: 0 4px;
    font-size: 12px;
    text-decoration: line-through;
    color: #9d9c9c;
    margin: 0;
}

.product-detail__line3-discount {
    padding: 0 4px;
    font-size: 16px;
    font-weight: 700;
    margin: 0;
}

.leave {
    opacity: 0;
    visibility: hidden;
}

.enter {
    opacity: 1;
    visibility: visible;
}

.product-action {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 3%;
    right: 5%;
    transition: opacity .3s ease,visibility .3s ease;
}

.product-action i{
    padding: 12px;
    background-color: #f3f3f3;
    margin: 6px 0;
    font-size: 14px;
    border-radius: 25px;
    transition: background-color 0.4s ease;
}

.product-action i:hover {
    background-color: #0d6efd;
    color: white;
    
}

.product-btn {
    position: absolute;
    bottom: -15px;
    /* height: 35px; */
    transition: opacity .3s ease,visibility .3s ease;
    /* background-color: #0d6efd;
    border: none;
    color: white;
    border-radius: 4px; */
}

.product-btn button {
    background-color: #0d6efd;
    border: none;
    color: white;
    border-radius: 4px;
    width: 137px;
    height: 30px;
}


.product-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 28px;
    padding-bottom: 20px;
    border-right: 1px solid rgb(234, 234, 234);

}
.product-container:hover {
    border-radius: 5px;
    box-shadow: 0px 1px 4px 1px rgba(187, 187, 187, 0.75);
    -webkit-box-shadow: 0px 1px 4px 1px rgba(187, 187, 187, 0.75);
    -moz-box-shadow: 0px 1px 4px 1px rgba(187, 187, 187, 0.75);
}

.product-detail {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}